import { Cubic, XY } from "./geometry.types"

/**
 * cast a ray from a particular point at a specified angle and distance. 
 * @param angle 
 * @param radius 
 * @param center 
 * @returns XY
 */
export const ray = (angle: number, radius: number, center: XY): XY => ([
	Math.cos(angle) * radius + center[0],
	Math.sin(angle) * radius + center[1]
]);

/**
 * Create the points to generate an ngon
 * @param sides 
 * @param radius 
 * @param center 
 * @returns XY[]
 */
export const ngon = (sides: number, radius: number, rotation: number, center: XY = [0, 0]): XY[] => {
	const radian = 2*Math.PI/sides;
	let points: number[][] = [];
	let minP: XY = [0, 0];
	let maxP: XY = [0, 0];
	for(let i = 0; i<sides; i++){
		const p = ray(radian * i + rotation, radius, center);
	}
	return new Array(sides).fill(0).map((_, i) => ray(radian * i + rotation, radius, center));
}

/**
 * Just a convenience method to create radians from degrees
 * @param deg 
 * @returns radians
 */
export const rad = (deg: number) => deg * (Math.PI/180);

/**
 * Returns a cubic [minX minY, distanceX, distanceY]
 * @param points 
 */
export const measure = (points: XY[]): Cubic => {
	let minP: XY = [0, 0];
	let maxP: XY = [0, 0];
	for(let i = 0; i<points.length; i++){
		minP = min(minP, points[i]);
	}
	return [...minP, ...multiply(maxP, 2)] as Cubic;
}

export const min = (p1: XY, p2: XY): XY => [Math.min(p1[0], p2[0]), Math.min(p1[1], p2[1])];

export const max = (p1: XY, p2: XY): XY => [Math.max(p1[0], p2[0]), Math.max(p1[1], p2[1])];

export const multiply = (point: XY, multiplier: XY | number) : XY => {
	const mult = typeof multiplier === 'number' ? new Array(2).fill(multiplier):multiplier;
	return point.map((v, i) => v*mult[i]) as XY;
}